export default {
	data() {
		return {
			micrDetail:'',
			micrRecommend:'',
			otherFoodList:'',
			urlV:''+new Date().getTime()
		}
	},
	onLoad(option){
		let { id } =option
		console.log(id)
		//这是请求的详情的列表的操作
		this.$request({
			url:"/front/food/queryFoodInfo",
			data:{
				foodId:id
			}
		}).then(res =>{
			this.micrDetail=res.data.data
			console.log(this.micrDetail)
			uni.setNavigationBarTitle({
				title:res.data.data.name
			})
		})
		//这是请求相关推荐的操作
		this.$request({
			url:"/front/food/queryRelativeFoodList",
			data:{
				foodId:id,
				page:'1',
				limit:'100',
			}
		}).then(res =>{
			console.log(res)
			this.otherFoodList=res.data.data
		})
	},
	methods: {
		ToMicrDetail(id){
			uni.navigateTo({
				url:'/pages/micrDetail/micrDetail?id='+id
			})
		}
	}
}
